  }
  

@media screen and (max-width : 900px){
    .FormDownload {
      grid-template: repeat(1, minmax(auto, 1fr)) / 1fr;
      gap: 0.8rem;
      margin-left: 10px;
    }
    .FormDownload__comprobantes{
        display: grid; 
        grid-template: auto/ auto; 
        gap:1rem;
        /* display: flex;
        flex-direction: column; */
        margin-bottom: 10px;
    }
    .FormDownload__input{
        display: grid; 
        grid-template: auto/auto; 
        /* display: flex;
        flex-direction: column; */
      
    }
    .FormDownload  button{
      width: 140px;
    }
    .video iframe{
      width:100%;
      height:300px;
    }
    
}

