/* Smartphones (portrait and landscape) ----------- */  

@media screen and (device-width: 320px) and (device-height: 568px) {

  .footer_info {
    display: grid;
    grid-template: repeat(1, minmax(auto, 1fr)) / 1fr;
    gap: 0.8rem;
    width: 300px;
  }
  .footer_info hr{
    background-color: #69B4CA;
    height: 2px;
    margin-left: 0%;
    width:7.5rem;
  }
  .footer__servicios_Margin{
    margin-right: 5px;
  }
  .footer__servicios{
    display: grid;
    grid-template: repeat(1, minmax(auto, 1fr)) / 1fr;
    gap: 0.8rem;
  
  }
  .footer_info p{
    color: #808495;
    font-size: 1.6rem;
  }
  .footer_info_hr{
    background-color: #69B4CA;
    /* margin-left: 0%; */
    /* width: 100% */
    width: 300px;
  }
  .footer_info_subtitulos{
    font-size: 1.7rem;
  }
  .footer_info a {
    font-size: 1.7rem;
  }
  .copyrigth p{
    font-size:1.5rem ;
  }
  .logo__Footer {
    width: 140px;
    height: 20px;
  }
  .fa-3x {
    font-size: 5em;
  }
}

