.menu-list {
    list-style: none;
    /* display: grid;
    grid-template: 1fr / repeat(5, minmax( auto, 200px)); */
    display: flex;
    flex-direction: row wrap;
    padding: 0;
    justify-items: center;
}

.menu-list li {
    align-self: center;
    border-radius: 5px;
}
 
.menu-list li a{
    position: relative;
    font-weight: bold;
    text-align: center;
    font-family:  'Open Sans', 'Roboto',sans-serif;
    text-decoration: none;
    font-weight: bold;
    margin: 0 20px;
}

.header__menu .menu-list li a {
  color: white;
}

.header__menu--positive .menu-list li a{
    color: black;
}

.menu-list li a::after{
  position: absolute;
  bottom: 7px;
  left: 0px;
  width: 100%;
  height: 3px;
  content: "";
  opacity: 0;
  -webkit-transition: opacity 0.3s ease 0s, -webkit-transform 0.3s ease 0s;
  transition: opacity 0.3s ease 0s, -webkit-transform 0.3s ease 0s;
  transition: opacity 0.3s ease 0s, transform 0.3s ease 0s;
  transition: opacity 0.3s ease 0s, transform 0.3s ease 0s, -webkit-transform 0.3s ease 0s;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
}

.header__menu .menu-list li a::after {
  background-color: white;
}

.header__menu--positive .menu-list li a::after {
  background-color: black;
}

.menu-list li a:hover::after{
  opacity: 1;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
}

.menu-list li a:hover{
  font-size: 1.1rem;
}

.menu-list__submenu{
  list-style: none;
  position: absolute;
  display: none;
  width: 13%;
  background-color: #0082ca;
  opacity: 0.8;
  margin-left: -0%px;
}

.menu-list__submenu p a{
  display: grid;
  grid-template: auto/auto;
  /* display: flex; */
  padding: 15px;
}

.menu-list li:hover .menu-list__submenu{
  display: grid;
  /* display: flex; */
}

.menu-list__logo{
  display: flex;
  font-weight: bold;
}


