/* Header block */
.header {
  position: fixed;
  z-index: 3;
  width: 100%;
  background:#119EEF;
  top: 0;
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
}

.header--positive {
  background: white;
  box-shadow: 9px 9px 9px 0 rgba(47, 44, 44, 0.2);
}

/* navbar element */
.header__navbar {
  /* display: grid;
  grid-template: 1fr / 1fr 2fr; */
  display: flex;
  flex-flow: row wrap;
  /* display: flex; */
  /* justify-content: center; */
  justify-content: space-around;
  align-items: initial;
  padding: 1rem;

}

/* brand element */
.header__brand {
  -webkit-animation: showBrand 2s forwards;
          animation: showBrand 2s forwards;
  justify-self: center;
  align-self: center;
  /* margin-right: 930px;
  margin-left: 300px; */
  align-items: initial;

}

.header__brand-image {
  width: 200px;
  height: 2.125rem;
}

/* menu element */
.header__menu {
  justify-self: center;
  align-self: center;
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
}

.header__menu--show .menu-list {
  display: grid;
  /* display: flex; */
}
/* keyframes for Brand animation */
@-webkit-keyframes showBrand {
  from {
    opacity: 0;
    -webkit-transform: scale(.5);
            transform: scale(.5);
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes showBrand {
  from {
    opacity: 0;
    -webkit-transform: scale(.5);
            transform: scale(.5);
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

